import React, { Component } from 'react'
import {Link,withRouter} from 'react-router-dom';
import TextFieldGroup from '../common/TextFieldGroup';
import TextAreaFieldGroup from '../common/TextAreaFieldGroup';
import {connect} from 'react-redux';
import PropTypes from 'prop-types';
import {addExperience} from '../../actions/profile';
class AddExperience extends Component {
    constructor(props){
        super(props);
        this.state={
            company:'',
            title:'',
            location:'',
            from:'',
            to:'',
            current:false,
            description:'',
            disabled:false ,
            errors:{}
        }
    }
    onCheck=(e)=>{
        console.log(this.state.disabled)
        this.setState({
            current:!this.state.current,
            disabled:!this.state.disabled
        })
    }
    onChange=(e)=>{
        this.setState({
           [e.target.name]:e.target.value
        })

    }
    componentWillReceiveProps(nextProps){
        if(nextProps.errors){
            this.setState({
                errors:nextProps.errors
            })
        }
    }
    onSubmit=(e)=>{
        e.preventDefault();
        let experience = {
          company:this.state.company,
          title:this.state.title,
          location:this.state.location,
          from:this.state.from,
          to:this.state.to,
          current:this.state.current,
          description:this.state.description,
        }
        this.props.addExperience(experience,this.props.history);
    }
    render() {
        const {errors} = this.state;
        return (
            <div className="add-experience">
                <div className="container">
                    <div className="row">
                        <div className="col-md-8 m-auto">
                            <Link className="btn btn-light" to="/dashboard"> Go Back </Link>
                            <h1 className="display-4 text-center">Add Experience</h1>
                            <p className="lead text-center">Add any job or position that you have had in the past or current</p>
                            <small className="d-block pb-3">* = required fields</small>
                            <form onSubmit={this.onSubmit}>
                            <TextFieldGroup
                            placeholder="*Company"
                            name="company"
                            value={this.state.company}
                            onChange={this.onChange}
                            error={errors.company}
                           />
                           <TextFieldGroup
                           placeholder="*Job title"
                           name="title"
                           value={this.state.title}
                           onChange={this.onChange}
                           error={errors.title}
                          />
                          <TextFieldGroup
                          placeholder="*Location"
                          name="location"
                          value={this.state.location}
                          onChange={this.onChange}
                          error={errors.location}
                         />
                         <h6>From date</h6>
                         <TextFieldGroup
                         type="date"
                         name="from"
                         value={this.state.from}
                         onChange={this.onChange}
                         error={errors.from}
                        />
                        <h6>To date</h6>
                        
                        <TextFieldGroup
                        type="date"
                        name="to"
                        value={this.state.to}
                        onChange={this.onChange}
                        error={errors.to}
                        disabled={this.state.disabled ? 'disabled' :''}
                       />
                       <div className="form-check mb-4">
                        <input className="form-check-input" 
                        name="current"
                        type="checkbox" 
                        value={this.state.current}
                        checked={this.state.current}
                        onChange={this.onCheck} 
                        id="current"
                        />
                        <label htmlFor="current" className="form-check-label">
                        Current job
                        </label>
                       </div>
                        <TextAreaFieldGroup
                        placeholder="Job Description"
                        name="description"
                        value={this.state.description}
                        onChange={this.onChange}
                        error={errors.description}
                        info="Tell us about the position"
                        />
                        <input type="submit" value="Submit" className="btn btn-info btn-block mt-4"/>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
AddExperience.propTypes={
    addExperience:PropTypes.func.isRequired,
    profile:PropTypes.object.isRequired,
    errors:PropTypes.object.isRequired,
}
const mapStateToProps = (state) => ({
    profile:state.profile,
    errors:state.errors
})


export default connect(mapStateToProps,{addExperience})(withRouter(AddExperience));


